@media only screen and (min-width: 993px) {
  .container {
    max-width: 100%;
  }
}

@media only screen and (max-width: 992px) {
  .container {
    max-width: 100%;
  }
}

@media only screen and (max-width: 768px) {
  .container {
    max-width: 100%;
  }
}

@media only screen and (max-width: 576px) {
  .container {
    max-width: 100%;
  }
}

@media only screen and (max-width: 320px) {
  .container {
    max-width: 100%;
  }
}
Nav {
  text-align: right;
}

#navbar {
  background-image: linear-gradient(120deg, steelblue 0%, skyblue 70%, white 100%);
}
#timing {
  background-image: url(/static/media/flowers.2a5cac3e.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  text-align: center;
  padding-bottom: 40px;
}

#titletiming {
  padding-top: 80px;
  font-family: 'Libre Baskerville', serif;
  font-weight: 400;
  font-size: 1.5em;
}

#titleremain {
  text-transform: capitalize;
}

#splash {
  background-image: url(/static/media/splashpic.4ef900dd.jpg);
  background-size: cover;
  background-position: center;
  background-position-y: 250px;
  background-repeat: no-repeat;
  width: auto;
  height: 1000px;
  text-align: center;
}

#titlesplash {
  padding-top: 60px;
  font-family: 'Great Vibes', cursive;
  font-weight: 400;
  font-size: 2em;
}

@media only screen and (max-width: 992px) {
  #splash {
    height: 700px;
  }  
}

@media only screen and (max-width: 768px) {
  #splash {
    height: 550px;
  }
}

@media only screen and (max-width: 576px) {
  #splash {
    height: 500px;
  }
}

@media only screen and (max-width: 320px) {
  #splash {
    height: 450px;
  }
}

#events {
  background-image: url(/static/media/events.cb00f43f.jpg);
  background-size: cover;
  background-position: center;
  background-position-y: 150px;
  background-repeat: no-repeat;
  text-align: center;
  padding-bottom: 60px;
}

#titleevents {
  padding-top: 60px;
  padding-bottom: 60px;
  font-family: 'Libre Baskerville', serif;
  font-weight: 400;
  font-size: 1.5em;
}

#ceremony, #reception {
  font-family: 'Great Vibes', cursive;
  font-size: 1.5em;
  padding-top: 60px;
}

#guestbook {
  text-align: center;
  padding-bottom: 60px;
}

#titleguestbook {
  padding-top: 60px;
  padding-bottom: 20px;
  font-family: 'Libre Baskerville', serif;
  font-weight: 400;
  font-size: 1.5em;
}

#titlesign {
  font-family: 'Great Vibes', cursive;
  font-size: 1.5em;
  padding-bottom: 20px;
}

li {
  list-style-type: none;
}

#signbutton {
  margin-bottom: 20px;
}

#thoughts {
  max-height: 600px;
  overflow: scroll;
  border: 10px solid skyblue;
  box-shadow: 0px 0px 10px 2px skyblue;
  padding: 0px;
  text-align: left;
}

#gallery {
  background-image: linear-gradient(120deg, steelblue 0%, skyblue 40%, white 100%);
  text-align: center;
  padding-bottom: 60px;
}

#titlegallery {
  padding-top: 60px;
  font-family: 'Libre Baskerville', serif;
  font-weight: 400;
  font-size: 1.5em;
}

#titlefile {
  padding-top: 20px;
  padding-bottom: 20px;
}

#uploadedimg {
  padding-bottom: 20px;
  max-width: 320px;
}

#fileselect {
  margin-top: 10px;
  width: 80%;
}

#titlecarousel {
  padding-top: 60px;
  padding-bottom: 20px;
  font-family: 'Great Vibes', cursive;
  font-size: 1.5em;
}

#carousel {
  position: relative;
  width: auto;
  height: auto;
  border: 10px solid steelblue;
  box-shadow: 0px 0px 10px 2px steelblue;
}

.carousel .carousel-indicators {
  display: none;
}

@media only screen and (max-width: 768px) {
  #fileselect {
    font-size: 12px;
  }
}

@media only screen and (max-width: 320px) {
  #fileselect {
    font-size: 12px;
  }
}

#registry {
  background-image: url(/static/media/registry.fd67ddc1.png);
  background-size: cover;
  background-position: left;
  background-position-y: 0px;
  background-repeat: no-repeat;
  width: auto;
  height: 200px;
  text-align: center;
}

#titleregistry {
  padding-top: 90px;
  font-family: 'Libre Baskerville', serif;
  font-weight: 400;
  font-size: 1.5em;
}

.forminputs {
  -webkit-align-items: center;
          align-items: center;
}

#titlersvp {
  padding-top: 60px;
  padding-bottom: 20px;
  font-family: 'Libre Baskerville', serif;
  font-weight: 400;
  font-size: 1.5em;
}

#footer {
  padding-bottom: 20px;
  text-align: center;
}

#titlefooter {
  padding-top: 60px;
  padding-bottom: 20px;
  font-family: 'Libre Baskerville', serif;
  font-weight: 400;
  font-size: 1.5em;
}

